@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@100;300;400;700&display=swap);
:root{
    --text-color: #232323;
    --default-font: Open Sans;
    --default-background: linear-gradient(180deg, rgba(240,126,5,1) 0%, rgba(244,186,2,1) 100%);
    --light-text: #fff;
    --input-background: #fff;
    --input-border: rgb(240,126,5);
    --input-border-focus: rgb(255 131 0);
}

body {
    background: linear-gradient(180deg, rgba(240,126,5,1) 0%, rgba(244,186,2,1) 100%);
    background: var(--default-background);
    background-attachment: fixed;
    margin: 0;
    font-family: Open Sans;
    font-family: var(--default-font);
}

.container {
    width: 45%;/* Takes half the screen */
    /* Center container */
    margin-right: auto; 
    margin-left: auto;
}

.container-title {
    padding-top: 15px;
    text-align: center;
    color: #fff;
    color: var(--light-text);
    font-weight: 700;
    font-size: 4em;
    padding-bottom: 0.5rem;
}

.line-break{
    padding: 1rem;
    display: block;
    border-bottom:1px solid #fff;
    width: 100%;
}
.SearchModule {
    width: 80%;
    padding-bottom: 1rem;
    margin: auto auto;
    position: relative;
}

.SearchModule input {
    font-family: var(--default-font);
    font-size: 1.5rem;
    font-weight: 100;
    width: 100%;
    height: 4rem;
    color: var(--text-color);
    background-color: var(--input-background);
    margin: 0;
    padding: 0 1rem;
    border-width: 1px;
    border-style: outset;
    border-color: var(--input-border);
    outline: none;
    box-sizing: border-box;
}

.SearchModule input:focus {
    outline-style: solid;
    outline-color: var(--input-border-focus);
    outline-width: 2px;
    outline-offset: 0;
}

.SearchModule::after {
    content: '';
    background: url(/static/media/search.dcc05c8f.svg);
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    right: 0;
    top: 0;
    margin: 0.8rem;
    width: 2rem;
    height: 2rem;
}

.WeatherList {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
}

.WeatherList h2 {
    color: var(--light-text);
    font-size: 2.5rem;
    line-height: 1rem;
}

.WeatherList table {
    display: inline;
}

.WeatherList table:first-child {
    padding-right: 2rem;
}

.WeatherList th, .WeatherList td {
    padding: 5px;
}

.WeatherList .side-tables table thead tr th{
    text-align: left;
    font-weight: 100;
    font-size: 0.9rem;
}

.WeatherList .side-tables table tbody tr td{
    text-align: left;
    font-weight: 700;
    color: var(--text-color);
    font-size: 0.9rem;
    padding-bottom: 1rem;
}
